.registration {
  position: relative;
  margin-top: -30px;
  background-color: $special-blue-700;
  border-radius: 0 0 30px 30px;
  z-index: 50;

  @media (min-width: $tablet-width) {
    margin-top: -60px;
    border-radius: 0 0 60px 60px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -56px;
    border-radius: 0 0 56px 56px;
  }
}

.registration__container {
  padding-top: 102px;
  padding-bottom: 27px;

  @media (min-width: $tablet-width) {
    padding: 137px 75px 57px;
  }

  @media (min-width: $desktop-width) {
    padding: 168px 134px 113px;
  }
}

.registration__title {
  margin-top: 0;
  margin-bottom: 19px;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  color: $special-yellow-900;

  @media (min-width: $tablet-width) {
    margin-bottom: 59px;
    font-size: 45px;
    line-height: 45px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 55px;
  }
}

.registration__form {
  position: relative;
  display: flex;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    width: 100%;
    margin-bottom: 60px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 68px;
  }
}

.registration__text {
  padding: 0 28px;
  margin-top: 0;
  margin-bottom: 29px;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  color: $basic-white;

  &:last-of-type {
    margin-bottom: 18px;
  }

  @media (min-width: $tablet-width) {
    padding: 0;
    margin-bottom: 65px;
    font-size: 28px;
    line-height: 32px;

    &:last-of-type {
      margin-bottom: 43px;
    }
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 58px;

    &:last-of-type {
      margin-bottom: 55px;
    }
  }
}

.registration__span {
  display: block;
}

.registration__link {
  color: $special-yellow-300;

  &:hover {
    color: $special-yellow-900;
  }

  &:active {
    opacity: 0.3;
  }
}

.registration__button {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 100%;
  padding: 0;
  font-size: 0;
  font-family: inherit;
  background-color: $special-yellow-300;
  border: none;
  border-radius: 12px;
  cursor: pointer;

  &:hover {
    background-color: $special-yellow-700;
  }

  &:active {
    color: rgba($special-blue-650, 0.3);

    svg {
      opacity: 0.3;
    }
  }

  @media (min-width: $tablet-width) {
    width: 80px;
    border-radius: 0 20px 20px 0;
  }

  @media (min-width: $desktop-width) {
    width: 324px;
    padding-left: 38px;
    padding-right: 38px;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    color: $special-blue-650;
    text-align: start;
  }
}

.registration__button-arrow {
  position: absolute;
  fill: $special-blue-650;
  top: 23px;
  right: 21px;

  @media (min-width: $tablet-width) {
    top: 32px;
    right: 33px;
    height: 16px;
  }

  @media (min-width: $desktop-width) {
    right: 34px;
    top: 35px;
  }
}

.registration__input {
  display: block;
  width: 100%;
  margin: 0 auto;
  padding: 22px 25px;
  font-family: $main-font;
  font-weight: 700;
  font-size: 18px;
  line-height: 18px;
  color: $basic-grey;
  border: none;
  border-radius: 12px;

  &:hover {
    background-color: $special-blue-10;
  }

  &::placeholder {
    color: rgba($basic-grey, 0.3);
  }

  @media (min-width: $tablet-width) {
    padding: 30px 25px 21px 36px;
    font-size: 24px;
    line-height: 24px;
    border-radius: 20px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 24px;
  }
}
