.search-form {
  @media (min-width: $tablet-width) {
    display: grid;
  }
}

.search-form__fieldset {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  border: none;

  @media (min-width: $tablet-width) {
    &:nth-child(2) {
      order: 1;
    }
  }

  @media (min-width: $desktop-width) {
    display: flex;
    align-items: center;

    &:first-child {
      margin-bottom: 17px;
    }
  }
}

.search-form__title,
.search-form__span {
  display: inline;
  margin: 0 0 8px;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  color: $special-blue-650;

  @media (min-width: $tablet-width) {
    padding-top: 26px;
    margin-bottom: 24px;
    font-size: 20px;
    line-height: 18px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 0;
    margin-bottom: 0;
    letter-spacing: 0;
  }
}

.search-form__title {
  @media (min-width: $desktop-width) {
    width: 164px;
  }
}

.search-form__span {
  letter-spacing: 0.07em;
  opacity: 0.3;

  @media (min-width: $desktop-width) {
    order: 1;
    width: 39px;
    margin-left: auto;
    opacity: 1;
  }
}

.search-form__checkbox {
  position: relative;
  display: block;

  @media (min-width: $tablet-width) {
    margin-bottom: 28px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 10px;
  }
}

.search-form__input:checked + .search-form__label::after {
  background-image: url("../img/svg/icon_check-mark.svg");
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 21px 21px;
}

.search-form__input {
  position: absolute;
  top: 3px;
  left: 0;
  width: 26px;
  height: 26px;
  margin: 0;
  opacity: 0;

  @media (min-width: $tablet-width) {
    top: -5px;
    width: 36px;
    height: 36px;
  }
}

.search-form__label {
  position: relative;
  padding: 4px 0 4px 39px;
  font-size: 15px;
  line-height: 15px;
  color: $special-blue-650;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 26px;
    height: 26px;
    border: 1px solid $special-blue-120;
    border-radius: 4px;
  }

  @media (min-width: $tablet-width) {
    padding: 10px 0 6px 53px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.1em;
    text-transform: uppercase;

    &::after {
      width: 36px;
      height: 36px;
    }
  }
}

.search-form__quantity {
  width: 100%;

  @media (min-width: $desktop-width) {
    width: 180px;
  }
}
