.page-main {
  margin-top: -3px;
  z-index: 1;
}

.page-main--catalog {
  background-color: $special-blue-10;

  @media (min-width: $desktop-width) {
    .page-main__background {
      background-color: $special-blue-700;
    }
  }
}

.page-main--form {
  background-color: $special-blue-10;

  .page-main__container {
    padding-top: 45px;
  }

  @media (min-width: $tablet-width) {
    .page-main__container {
      padding: 95px 75px 66px;
    }

    .page-main__title::after {
      width: 358px;
    }
  }

  @media (min-width: $desktop-width) {
    .page-main__container {
      padding: 120px 124px 68px;
    }
  }
}

.page-main__header {
  position: relative;
  z-index: 10;
}

.page-main__background {
  position: relative;
  padding-bottom: 4px;
  z-index: 5;
  background-color: $special-yellow-200;

  @media (min-width: $tablet-width) {
    padding-bottom: 0;
    background-color: $special-blue-700;
    border-radius: 0 0 60px 60px;
  }

  @media (min-width: $desktop-width) {
    border-radius: 0 0 56px 56px;
  }
}

.page-main__container {
  padding-top: 46px;
  padding-bottom: 11px;
  background-color: $special-blue-700;
  border-radius: 0 0 22px 22px;

  &--catalog {
    .page-main__title::after {
      width: 172px;
    }
  }

  @media (min-width: $tablet-width) {
    padding: 42px 75px 67px;
    border-radius: 0 0 59px 59px;

    &--catalog {
      display: grid;
      grid-template-columns: 1fr 1fr;
      align-items: center;
      padding-top: 127px;

      .page-main__title::after {
        width: 288px;
      }
    }
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 84px;
    padding-bottom: 64px;

    &--catalog {
      padding: 97px 75px 38px 126px;
      min-height: 247px;

      .page-main__title::after {
        width: 288px;
        bottom: -70px;
      }
    }
  }
}

.page-main__title {
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
  color: $basic-white;

  &::after {
    content: "";
    position: absolute;
    bottom: -14px;
    left: 0;
    width: 214px;
    height: 5px;
    background-color: $special-yellow-900;
    border-radius: 4px;
  }

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;

    &::after {
      bottom: -71px;
      width: 287px;
      height: 7px;
    }
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/2;

    &::after {
      bottom: -72px;
      width: 289px;
      height: 8px;
    }
  }
}

.page-main__link {
  position: relative;
  display: none;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  color: $basic-white;

  &::after {
    content: "";
    position: absolute;
    right: 0;
    width: 32px;
    height: 16px;
    background-image: url("../img/svg/icon_arrow_bend.svg");
    background-repeat: no-repeat;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    display: block;
    grid-column: 2/3;
    padding: 21px 61px 15px 0;
    margin-left: auto;
  }

  @media (min-width: $desktop-width) {
    margin-right: 48px;
  }
}

.page-main__body {
  margin-top: -21px;
  background-color: $special-blue-10;

  @media (min-width: $tablet-width) {
    margin-top: -59px;
  }
}

.page-main__content {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 85px;
    padding-top: 150px;
  }
}

.page-main__body-container {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 950px 344px;
    column-gap: 86px;
    padding: 98px 60px 0;
  }
}

.page-main__companion-filter {
  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    padding: 0;
    margin-right: 0;
    margin-left: auto;
  }
}

.page-main__filter-profiles {
  @media (min-width: $desktop-width) {
    grid-column: 1/2;
    grid-row: 1/2;
    padding-right: 0;
    padding-left: 0;
    margin-right: auto;
    margin-left: 0;
  }
}
